import React, { Component } from "react";
import BootstrapTable from '../../node_modules/react-bootstrap-table-next';
import { Table } from '../../node_modules/react-bootstrap';
import { Form , Button , Col , Row} from 'react-bootstrap';
import './ticket_page.css';


export default class NewTicket extends Component {

 constructor(props) {
  super(props)
  this.state = {
    ticket : {'id':'','department':"0",'category':"0",'lab_url':'','subject':'','description':'','email':email,'username':username},
    departments : [],
    categories : [],
    errors:{'department':'','category':'','lab_url':'','subject':'','description':''},
  }
      this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
 }

   handleChange = (e) => {
  let newState = {'ticket':this.state.ticket}
  newState['ticket'][e.target.name] = e.target.value
  this.setState(newState)
 }

  handleSubmit = (e, message) => {
  e.preventDefault()
  var form_data = new FormData(document.getElementById("add_ticket"));
  form_data.append('csrfmiddlewaretoken',$('[name="csrfmiddlewaretoken"]').val())


  $.ajax({
   url: '/tickets/add_ticket/',
   type: 'POST',
       header:{
    'X-CSRFTOKEN': $('[name="csrfmiddlewaretoken"]').val()
    },
   processData: false,
    contentType: false,
    cache: false,
    enctype: 'multipart/form-data',
   data: form_data,
   success: function(data) {
       $('.is-invalid').removeClass('is-invalid')
      this.props.history.push('/tickets')
    }.bind(this),
   error: function(data) {
        var new_state = {'errors':{}}
        $.each(data.responseJSON, function (error_name, error_msg) {
            new_state['errors'][error_name] = error_msg
            $('#'+error_name).addClass('is-invalid')
        }.bind(this))
        this.setState(new_state)
   }.bind(this)
  })
  }


    componentDidMount() {
        fetch('/tickets/departments/?format=json').then((res)=>res.json().then((data)=>{
            this.setState({ departments : data });
        })).then(() => {
            fetch('/tickets/categories/?format=json').then((res)=>res.json().then((data)=>{
                this.setState({ categories : data });
            }))
        })
      }

     render() {
        return(<div className="auth-wrapper mx-auto col-md-12" style={{marginTop:10 + 'em'}}>
 <Row>
  <Col sm={6} className="coaching-green-test-2">
  <Form id="add_ticket" onSubmit={this.handleSubmit} >
    <Form.Group>
    <Form.Label column sm={5}>
      <h4>Ticket Information</h4>
    </Form.Label>
    </Form.Group>

  <Form.Group>
    <Form.Label column sm={5}>
      Ticket
    </Form.Label>
    <Col sm={10}>
    <Form.Control type="text" name="id" onChange={this.handleChange} value={"#" + this.state.ticket.id} placeholder="ticket-id" readOnly />
    </Col>
  </Form.Group>


      <Form.Group >
    <Form.Label column sm={5}>Department</Form.Label>
      <Col sm={10}>
      <Form.Control as="select" name="department" id="department" onChange={this.handleChange} value={this.state.ticket.department.id} className="mr-sm-2" custom>
      <option value="0">---</option>
      {this.state.departments.map(department => (
        <option value={department.id}>{department.name}</option>
      ))}
      </Form.Control> <div className="invalid-feedback">{this.state.errors.department}</div>
      </Col>
    </Form.Group>


    <Form.Group >
    <Form.Label column sm={5}>Category</Form.Label>
      <Col sm={10}>
      <Form.Control as="select" name="category" id="category" onChange={this.handleChange} value={this.state.ticket.category.id} className="mr-sm-2" custom>
      <option value="0">---</option>
      {this.state.categories.map(category => (
        <option value={category.id}>{category.name}</option>
      ))}
      </Form.Control>
      <div className="invalid-feedback">{this.state.errors.category}</div>
      </Col>
    </Form.Group>

  <Form.Group >
    <Form.Label column sm={5}>
      Lab URL
    </Form.Label>
    <Col sm={10}>
      <Form.Control type="url" id="lab_url" name="lab_url" placeholder="Lab URL" onChange={this.handleChange} value={this.state.ticket.lab_url} />
        <div className="invalid-feedback">{this.state.errors.lab_url}</div>
    </Col>
  </Form.Group>

  <Form.Group >
    <Form.Label column sm={5}>
      Subject
    </Form.Label>
    <Col sm={10}>
      <Form.Control type="text" name="subject" id="subject" placeholder="Subject" onChange={this.handleChange} value={this.state.ticket.subject}/>
        <div className="invalid-feedback">{this.state.errors.subject}</div>
    </Col>
  </Form.Group>

    <Form.Group>
    <Form.Label column sm={5}>
      Description
    </Form.Label>
    <Col sm={10}>
      <Form.Control as="textarea" id="description" name="description" placeholder="Description"  onChange={this.handleChange} rows={3} cols={5} value={this.state.ticket.description}/>
    <div className="invalid-feedback">{this.state.errors.description}</div>
    </Col>
  </Form.Group>

    <Form.Group>
    <Form.Label column sm={5}>Priority</Form.Label>
      <Col sm={10}>
      <Form.Control as="select" name="priority" id="priority" value={this.state.ticket.priority} onChange={this.handleChange} className="mr-sm-2" id="inlineFormCustomSelect" custom>
      <option value="0">---</option>
      <option value="1">HIGH - Production System Down</option>
      <option value="2">MED - System Impaired</option>
      <option value="3">LOW - General Guidence</option>
      </Form.Control>
      <div className="invalid-feedback">{this.state.errors.priority}</div>
      </Col>
    </Form.Group>

  <Form.Group >
  <br/>
    <Col sm={{ span: 1, offset: 0 }}>
      <Button type="submit">Submit</Button>
    </Col>
  </Form.Group>
</Form>
</Col>
<Col sm={6} className="coaching-green-test-2">
  <Form >
    <Form.Group>
    <Form.Label column sm={5}>
      <h4>User Information</h4>
    </Form.Label>
    </Form.Group>

  <Form.Group >
    <Form.Label column sm={5}>
      User Name
    </Form.Label>
    <Col sm={10}>
      <Form.Control type="text" name="name"  placeholder="name" value={this.state.ticket.username} disabled/>
    </Col>
  </Form.Group>

  <Form.Group >
    <Form.Label column sm={5}>
      User Email
    </Form.Label>
    <Col sm={10}>
      <Form.Control type="text" name="email"  placeholder="Email" value={this.state.ticket.email} disabled/>
    </Col>
  </Form.Group>
    </Form>

</Col>
</Row>
</div>)
  }
}