import React, { Component } from "react";



export default class Login extends Component {
 constructor(props) {
  super(props)
  this.state = {
   email: {'value':'','error':''},
   password: {'value':'','error':''},
   __all__:{'value':"","error":""}
  }
  this.handleChange = this.handleChange.bind(this)
  this.handleSubmit = this.handleSubmit.bind(this)
  console.log(this.props);
 }

 handleChange = (e) => {
  let newState = {}
  newState[e.target.name] = {}
  newState[e.target.name]['value'] = e.target.value
  newState[e.target.name]['error'] = this.state[e.target.name]['error']
  this.setState(newState)
 }

 handleSubmit = (e, message) => {
  e.preventDefault()

  let formData = {
   email: this.state.email.value,
   password:this.state.password.value,
  }

  if (formData.email.length < 1 || formData.password.length < 1) {
   return false
  }

  $.ajax({
   url: '/accounts/login/',
   dataType: 'json',
   type: 'POST',
   data: formData,
   success: function(data) {
       $('.is-invalid').removeClass('is-invalid')
       $('#logout_link').css('display','initial')
       $('#new_ticket_link').css('display','initial')
       $('#manage_ticket').css('display','initial')
       this.props.history.push('/tickets')
    }.bind(this),
   error: function(data) {
        var new_state = {}
        $.each(data.responseJSON, function (error_name, error_msg) {
            new_state[error_name] = {}
            new_state[error_name]['value'] = this.state[error_name]['value']
            new_state[error_name]['error'] = error_msg
            $('#'+error_name).addClass('is-invalid')
        }.bind(this))
        this.setState(new_state)
   }.bind(this)
  })
 }


    render() {
        return (
              <div className="auth-wrapper">
                <div className="auth-inner">
            <form onSubmit={this.handleSubmit}>
                <h3>Sign In</h3>

                <div className="form-group">
                    <label>Email address</label>
                    <input type="email" name="email" id="email" onChange={this.handleChange} value={this.state.email.value} className="form-control" placeholder="Enter email" />
                    <div className="invalid-feedback">{this.state.email.error}</div>
                </div>

                <div className="form-group">
                    <label>Password</label>
                    <input type="password" name="password" id="password" onChange={this.handleChange} value={this.state.password.value} className="form-control" placeholder="Enter password" />
                    <div className="invalid-feedback">{this.state.password.error}</div>
                </div>


                <div className="form-group">
                    <input style={{display:"none"}} id="__all__"/>
                    <div style={{textAlign:"center"}} className="invalid-feedback">{this.state.__all__.error}</div>
                </div>

                <button type="submit" className="btn btn-primary btn-block">Submit</button>
            </form></div></div>
        );
    }
}