import React, { Component } from "react";
import BootstrapTable from '../../node_modules/react-bootstrap-table-next';
import { Table } from '../../node_modules/react-bootstrap';
import { Form , Button , Col , Row } from 'react-bootstrap';
import './ticket_page.css';


export default class TicketDetails extends Component {

 constructor(props) {
  super(props)
  this.state = {
    ticket : {'id':'','department':"0",'category':"0",'lab_url':'','subject':'','description':'','email':'','name':''},
    departments : [],
    categories : [],
    errors:{'department':'','category':'','lab_url':'','subject':'','description':''},
  }
    this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
 }

   handleChange = (e) => {
  let newState = {'ticket':this.state.ticket}
  newState['ticket'][e.target.name] = e.target.value
  this.setState(newState)
 }

  handleSubmit = (e, message) => {
  e.preventDefault()

  var form_data = new FormData(document.getElementById("update_ticket"));
  form_data.append('csrfmiddlewaretoken',$('[name="csrfmiddlewaretoken"]').val())

  $.ajax({
   url: '/tickets/update_ticket/',
   type: 'POST',
       header:{
    'X-CSRFTOKEN': $('[name="csrfmiddlewaretoken"]').val()
    },
    processData: false,
    contentType: false,
    cache: false,
    enctype: 'multipart/form-data',
   data: form_data,
   success: function(data) {
       $('.is-invalid').removeClass('is-invalid')
      this.props.history.push('/tickets')
    }.bind(this),
   error: function(data) {
        var new_state = {'errors':{}}
        $.each(data.responseJSON, function (error_name, error_msg) {
            new_state['errors'][error_name] = error_msg
            $('#'+error_name).addClass('is-invalid')
        }.bind(this))
        this.setState(new_state)
   }.bind(this)
  })

  }


     _fetch_details(id){
        fetch('/tickets/fetch_ticket/' + id).then((res)=>res.json().then((data)=>{
            this.setState({ ticket : data });
        }));
     }

    componentDidMount() {
        fetch('/tickets/departments/?format=json').then((res)=>res.json().then((data)=>{
            this.setState({ departments : data });
        })).then(() => {
            fetch('/tickets/categories/?format=json').then((res)=>res.json().then((data)=>{
                this.setState({ categories : data });
            }))
        }).then(() => {
            this._fetch_details(this.props.match.params.ticket_id);
        });
      }

     render() {
        return(<div className="auth-wrapper mx-auto col-md-12" style={{marginTop:10 + 'em'}}>
   <Row>
  <Col sm={6} className="coaching-green-test-2">
  <Form id="update_ticket" onSubmit={this.handleSubmit}>
    <Form.Group>
    <Form.Label column sm={5}>
      <h4>Ticket Information</h4>
    </Form.Label>
    </Form.Group>

  <Form.Group>
    <Form.Label column sm={5}>
      Ticket
    </Form.Label>
   <Col sm={10}>
    <Form.Control name="id" type="text" onChange={this.handleChange}  value={"#" + this.state.ticket.id} placeholder="ticket-id" readOnly />
    </Col>
  </Form.Group>


      <Form.Group >
    <Form.Label column sm={5}>Department</Form.Label>
     <Col sm={10}>
      <Form.Control as="select" value={this.state.ticket.department.id} onChange={this.handleChange} id="department"  name="department" className="mr-sm-2" custom>
      <option value="0">---</option>
      {this.state.departments.map(department => (
        <option value={department.id}>{department.name}</option>
      ))}
      </Form.Control>
      <div className="invalid-feedback">{this.state.errors.department}</div>
      </Col>
    </Form.Group>


    <Form.Group >
    <Form.Label column sm={5}>Category</Form.Label>
     <Col sm={10}>
      <Form.Control as="select" id="category" name="category" onChange={this.handleChange}  value={this.state.ticket.category.id} className="mr-sm-2" custom>
      <option value="0">---</option>
      {this.state.categories.map(category => (
        <option value={category.id}>{category.name}</option>
      ))}
      </Form.Control>
      <div className="invalid-feedback">{this.state.errors.category}</div>
      </Col>
    </Form.Group>

  <Form.Group >
    <Form.Label column sm={5}>
      Lab URL
    </Form.Label>
   <Col sm={10}>
      <Form.Control type="url" id="lab_url" name="lab_url" onChange={this.handleChange}  placeholder="Lab URL" value={this.state.ticket.lab_url} />
        <div className="invalid-feedback">{this.state.errors.lab_url}</div>
    </Col>
  </Form.Group>

  <Form.Group >
    <Form.Label column sm={5}>
      Subject
    </Form.Label>
   <Col sm={10}>
      <Form.Control type="text" id="subject" name="subject" onChange={this.handleChange}  placeholder="Subject" value={this.state.ticket.subject}/>
        <div className="invalid-feedback">{this.state.errors.subject}</div>
    </Col>
  </Form.Group>

    <Form.Group>
    <Form.Label column sm={5}>
      Description
    </Form.Label>
   <Col sm={10}>
      <Form.Control as="textarea" id="description" name="description" className="description" onChange={this.handleChange}  placeholder="Description"  rows={3} cols={5} value={this.state.ticket.description}/>
        <div className="invalid-feedback">{this.state.errors.description}</div>
    </Col>
  </Form.Group>

    <Form.Group>
    <Form.Label column sm={5}>Priority</Form.Label>
     <Col sm={10}>
      <Form.Control as="select" id="priority" name="priority" onChange={this.handleChange}  value={this.state.ticket.priority} className="mr-sm-2" id="inlineFormCustomSelect" custom>
      <option value="0">---</option>
      <option value="1">HIGH - Production System Down</option>
      <option value="2">MED - System Impaired</option>
      <option value="3">LOW - General Guidence</option>
      </Form.Control>
      </Col>
      <div className="invalid-feedback">{this.state.errors.priority}</div>
    </Form.Group>

  <Form.Group >
    <Col sm={{ span: 1, offset: 0 }}>
    </Col>
  </Form.Group>
</Form>
</Col>
<Col sm={6} className="coaching-green-test-2">
 <Form >
    <Form.Group>
    <Form.Label column sm={5}>
      <h4>User Information</h4>
    </Form.Label>
    </Form.Group>

  <Form.Group >
    <Form.Label column sm={5}>
      User Name
    </Form.Label>
    <Col sm={10}>
      <Form.Control type="text" name="name"  placeholder="name" value={this.state.ticket.name} disabled/>
    </Col>
  </Form.Group>

  <Form.Group >
    <Form.Label column sm={5}>
      User Email
    </Form.Label>
    <Col sm={10}>
      <Form.Control type="text" name="email"  placeholder="Email" value={this.state.ticket.email} disabled/>
    </Col>
  </Form.Group>
    </Form>
</Col>
</Row>
</div>)
  }
}